module.exports = function (SmartlingBaseOptions, SmartlingTranslationStates) {
  class SmartlingImportOptions extends SmartlingBaseOptions {
    constructor(props) {
      super(props);

      this.localeId = props.localeId;
      this.fileType = props.fileType || 'json';
      this.content = props.content;
      this.contentType = props.contentType || 'application/json';
      this.overwrite = props.overwrite || false;
      this.translationState = props.translationState || SmartlingTranslationStates.published;
    }
  }

  return SmartlingImportOptions;
};
